.App {
  text-align: center;
  font-family: Verdana, sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.ant-table-content{
  margin: 25px;
}

.fund-name{
  font-size: 16px;
  font-weight: bold;
  text-align: left;
  margin-left: 25px;
}

.ant-table-thead > tr > th, .ant-table-tbody > tr > td{
  padding: 1px 1px !important;
  text-align: right !important;
}
 
.ant-table-tbody > tr > td:first-child, .ant-table-tbody > tr > td:nth-child(2),
.ant-table-thead > tr > th:first-child, .ant-table-thead > tr > th:nth-child(2) {
  text-align: left !important;
}

.ant-table-thead > tr > th {
  padding: 2px 5px !important;
}


.red{
  color: red; 
}
.green {
   color :green; 
}

.table_title{
  font-weight: bold;
  margin-top: 20px;
  text-align: left;
  margin-left: 15px;
  font-size: 16px;
}